body {
  letter-spacing: .08rem;
  height: 100vh;
  background: url("vitality-stylish-handsome-athlete-empty.1aa12fd0.jpg") center / cover no-repeat;
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.calendar {
  justify-content: center;
  display: flex;
}

input, button, a {
  border-radius: 4px;
  padding: .2em .8em;
  font-size: 1.5rem;
  transition: all .3s;
}

input {
  width: 310px;
  height: 50px;
  border: none;
}

button {
  width: 150px;
  height: 50px;
  cursor: pointer;
  border: none;
  margin-left: 25px;
}

a {
  color: #fff;
  background-color: #000;
  margin: .5rem;
  font-size: 1rem;
  text-decoration: none;
  position: absolute;
  top: 0;
  left: 0;
}

button:hover:not([disabled]), a:hover {
  color: #ecbc1f;
  background-color: gray;
}

.timer {
  text-align: center;
  width: 480px;
  text-transform: uppercase;
  flex-direction: row;
  margin: 20px auto;
  font-size: 1.25rem;
  display: flex;
}

.value {
  font-size: 5rem;
}

